.landing-page {
  font-family: 'Myriad Pro', sans-serif;
  text-align: center;
  color: #333;
}


.navbar {
  position: fixed;
  width: 100%;
  height: 30px;
  top: 0;
  left: 0;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0b0b0d; 
  transition: background-color 0.5s ease, padding 0.3s ease; 
  z-index: 1000;
}

.navbar.scrolled {
  background-color: transparent;
}

.navbar ul {
  display: flex;
  gap: 20px;
  transition: gap 0.3s ease; 
}


.navbar:not(.scrolled) ul {
  gap: 50px; 
}

.navbar.scrolled ul {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 15px;
  padding: 5px 15px;
}

.navbar ul li {
  list-style: none;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  padding: 10px;
  font-weight: bold;
  display: block;
  transition: color 0.3s ease;
}

.navbar ul li a:hover {
  color: #f39c12; /* Hover color */
}

nav ul li a::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  width: 8px;
  height: 8px;
  background: #f2662a;
  border-radius: 50%;
  transform: translateX(-50%) scale(0);
  transition: transform 0.2s ease-in-out;
}

nav ul li a:hover::after {
  transform: translateX(-50%) scale(1);
}



.contact-btn, .read-more {
  background: #f2662a;
  border: none;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 0px;
  margin-right: 30px;
}

.hero-section {
  position: relative;
  margin: 0;
}

.car-container {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
  background-image: url('../assets/background.png'); 
  background-size: 100% 100%;  
  background-position: top left;
  background-repeat: no-repeat;
  margin-top: 40px; 
  
  top: 50px;
}

.car {
  position: absolute;
  top: 0px;
  right: -100%;
  width: 700px;
  height: auto;
  transition: right 1.8s cubic-bezier(0.3, 1, 0.3, 1);
  filter: blur(10px);
}

.car img {
  width: 100%;
  height: auto;
}

.car.animate {
  right: calc(50% - 400px);
  top: 50px; 
  filter: blur(0);
  animation: bounce 0.5s ease-out 1.5s;
}

@keyframes bounce {
  0% { transform: translateX(0); }
  50% { transform: translateX(-10px); }
  100% { transform: translateX(0); }
}

h1 {
  font-size: 32px;
  margin-top: 80px;
}

.highlight {
  color: #5d2d90;
}

.highlight-orange {
  color: #f2662a;
}

.smart-listening-section {
  padding: 6rem 3rem;
  background-color: #f9f9f9;
  text-align: center;
  position: relative;
}

.smart-listening-section h2 {
  font-size: 3rem;
  margin-bottom: 3rem;
  color: #333;
  font-weight: 700;
}

.listening-features {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
  position: relative;
}

.feature-card {
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1);
  padding: 2.5rem;
  width: 100%;
  max-width: 600px; 
  text-align: left;
  position: relative;
  margin-bottom: 60px;
  transition: all 0.3s ease;
  opacity: 1; 
}

.feature-card.sticky {
  position: sticky;
  top: 0;
  z-index: 10;
}

.feature-card h3 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #ff6f00;
  font-weight: 600;
}

.feature-card p {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.8;
}

.feature-benefits {
  list-style: none;
  padding: 0;
  margin-top: 1.5rem;
}

.feature-benefits li {
  font-size: 1rem;
  color: #444;
  margin-bottom: 0.8rem;
  display: flex;
  align-items: center;
}

.feature-benefits li::before {
  content: "âœ”";
  color: #28a745;
  margin-right: 0.5rem;
  font-size: 1.2rem;
}

.logo img {
  bottom: -30px;
  height: 30px; 
  width: auto; 
  margin-left: 30px; 
  border-radius: 50%; 
  transition: all 0.3s ease; 
}


.navbar {
  transition: background-color 0.5s ease, padding 0.3s ease;
}
